:root {
  --main-text-color: #fff;
  --main-family: "Epilogue", sans-serif;
  --trasform: 250ms cubic-bezier(.4, 0, .2, 1);
  --main-button-color: #1e2827;
  --main-background-color: #141414;
}

.navbar-container {
  background-color: #141414;
  border: 0;
  padding: 29px 20px;
}

.navbar-container .logo-nav-container {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.navbar-container .logo-nav-container .logo {
  font-family: var(--main-family);
  color: var(--main-text-color);
  letter-spacing: -.03em;
  text-align: left;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 4px;
  font-size: 12px;
  font-weight: 400;
  line-height: 15.37px;
  display: flex;
}

.navbar-container .logo-nav-container .logo .iconlogo svg {
  height: 14px;
  width: 14px;
}

.navbar-container .nav-menu-container .navigation-bar {
  display: none;
}

.navbar-container .nav-menu-container .mobile-menu-icon svg {
  height: 24px;
  width: 24px;
}

.navbar-container .nav-phone {
  display: none;
}

.navbar-container .js-menu-container {
  display: none;
  transform: scale(2);
}

.navbar-container .js-menu-container.is-open {
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: #fff;
  flex-direction: column;
  transition-duration: .51s;
  transition-timing-function: ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: scale(1);
}

.navbar-container .js-menu-container .js-close-menu {
  background: none;
  border: none;
  margin-bottom: 30px;
}

.navbar-container .js-menu-container .js-close-menu svg {
  box-sizing: content-box;
}

@media screen and (min-width: 768px) {
  .navbar-container {
    justify-content: space-between;
    padding: 34px 32px;
    display: flex;
  }

  .navbar-container .logo-nav-container {
    justify-content: center;
    align-items: center;
    gap: 80px;
    display: flex;
  }

  .navbar-container .logo-nav-container .logo {
    font-size: 15px;
  }

  .navbar-container .logo-nav-container .logo .iconlogo svg {
    height: 19px;
    width: 19px;
  }

  .navbar-container .nav-menu-container .mobile-menu-icon {
    display: none;
  }

  .navbar-container .nav-menu-container .navigation-bar {
    font-family: var(--main-family);
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .navbar-container .nav-menu-container .navigation-bar ul {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 40px;
    list-style-type: none;
    display: flex;
  }

  .navbar-container .nav-menu-container .navigation-bar ul a {
    color: var(--main-text-color);
    letter-spacing: 0;
    text-align: center;
    font-size: 9px;
    font-weight: 400;
    line-height: 12px;
  }

  .navbar-container .nav-phone {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .navbar-container .nav-phone .btnservice {
    width: 159px;
    height: 48px;
    font-family: var(--main-family);
    letter-spacing: 0;
    text-align: center;
    background-color: var(--main-button-color);
    border: 0;
    border-radius: 8px;
    padding: 0;
    font-size: 9px;
    font-weight: 400;
    line-height: 12px;
  }

  .navbar-container .nav-phone .btnservice a {
    color: var(--main-text-color);
  }
}

@media screen and (min-width: 1178px) {
  .navbar-container {
    padding: 34px;
  }

  .navbar-container .logo-nav-container {
    margin-left: 98px;
  }

  .navbar-container .nav-phone .btnservice {
    margin-right: 98px;
  }
}

